import { Component, h, Element, Prop } from '@stencil/core';

@Component({
  tag: 'ks-order-summary-field',
})
export class OrderSummaryField {

    @Element() root: HTMLElement;

    componentDidLoad(){
        this.root.style.display = "block"
    }

    @Prop() dark: boolean = false;
    @Prop() large: boolean = false;
    @Prop() single: boolean = false;

    render() {
        const bgColor = this.dark ? "#252525" : "#f2f2f2";
        const color = this.dark ? "white" : "inherit";
        const font = this.large ? "20px" : "inherit";

        if(this.single) return (
            <div class={"uk-padding-small " + (this.large ? "ks-text-decorated" : "")}
                style={{backgroundColor: bgColor, color: color, fontSize: font}}>

                <slot />
            </div>
        );

        return(
            <div class={"uk-padding-small uk-flex uk-flex-wrap uk-child-width-1-1 uk-child-width-auto@m uk-flex-between " + (this.large ? "ks-text-decorated" : "")}
                style={{backgroundColor: bgColor, color: color, fontSize: font}}>

                <div class="uk-text-center uk-text-left@m" style={{minWidth: "150px"}}><slot name="left"></slot></div>
                <div class="uk-text-center uk-text-right@m"><slot name="right"></slot></div>
            </div>
        );
    }
}